#skills 
{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 0 auto;
}

.skillTitle 
{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;

}

.skillDesc
{
    font-weight: 400;
    font-size: 1rem;
    max-width: 50rem;
    padding: 0 2rem;
}
.skillBars
{
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;
}

.skillbar {
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background: rgb(50,50,50);
}
.skillbar:hover {
    border-bottom: 2px solid yellow;
    border-right: 2px solid yellow;
    box-shadow: 5px 5px 15px yellow;
}

.skillBarImg
{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;
  
}

.skillBarText>p {
    font-size: 0.9rem;
    font-weight: 200;
}

@media screen and (max-width:720px) {
    .skillBarText>p {
        font-size: 3vw;
    }
    
}

@media screen and (max-width:480px) {
    .skillBarText>p {
        font-size: 3vw;
    }
    .skillBarImg {
        height: 2.25rem;
        width: 2.25rem;

    }
    .skillBarText>h2 {
        font-size: 5vw;
    }

}

#java {
        object-fit: cover;
        height: 4rem;
        width: 4rem;
        margin-right: 2rem;
        border-radius: 2rem;


    }

   